<template>
  <el-form
    class="form"
    :inline="true"
    v-loading="loading"
    @submit.native.prevent="onSubmit"
    :model="form"
    label-width="100px"
  >
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="旧密码">
          <el-input v-model.trim="form.passwd" placeholder="请输入内容"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="新密码">
          <el-input v-model.trim="form.npasswd" placeholder="请输入内容"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="确认密码">
          <el-input v-model.trim="form.rpasswd" placeholder="请输入内容"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">修改</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch3 from '../../utils/fetch3'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      form: {
        passwd: '',
        npasswd: '',
        rpasswd: '',
      },
      loading: false,
    }
  },
  mounted() {},
  methods: {
    onSubmit() {
      this.loading = true

      if (!this.form.passwd) {
        Message.error('请输入旧密码')
        this.loading = false
        return
      }

      if (!this.form.npasswd) {
        Message.error('请输入新密码')
        this.loading = false
        return
      }

      if (this.form.npasswd !== this.form.rpasswd) {
        Message.error('新密码和确认密码不一致')
        this.loading = false
        return
      }

      Fetch3('/sys/user/setpwd', {
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
          localStorage.removeItem('pstt')
          this.$store.commit('setLogout')
          this.$store.commit('cleanConfig')
          location.href = '/'
        })
        .catch(() => (this.loading = false))
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
